import Axios from 'axios'

const state = () => ({
  course: {},
  message: null,
  info: {},
  status: null,
  error: null,
  code: null,
  loading: false,
  host: 'https://apifirstswajar.lan.go.id'
  // host: "http://10.67.0.152:8080"
  // host: "http://localhost:8080"
})

const mutations = {
  set_course(state, course){
    state.course = course
  },
  set_message(state, message){
    state.message = message
  },
  set_info(state, info){
    state.info = info
  },
  set_status(state, status){
    state.status = status
  },
  set_error(state, error){
    state.error = error
  },
  set_code(state, code){
    state.code = code
  },
  set_loading(state, loading){
    state.loading = loading
  }
}

const actions = {
  get_all_course({commit, state}) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      let url = state.host+"/nJ403/7Y4dD2n3J9";

      Axios.get(url, {headers: headers})
      .then(response => {

        // let all_course = response.data;
        // let course = [];

        // for (let i = 0; i < all_course.length; i++) {
        //   const element = all_course[i];
        //   course.push({code: element.code, nip: element.nip, pelatihan: element.pelatihan, angkatan: element.angkatan, evaluasi_akademik:JSON.parse(element.evaluasiAkademik), tanggal:JSON.parse(element.tanggal), keterangan: element.keterangan})
        // }

        commit('set_loading', false);
        // commit('set_course', course)
        commit('set_course', response.data)
        resolve(response)
      })
      .catch(error => {
        console.log(error.response.data.message || error.message)
        commit('set_loading', false)
        commit('set_error', error)
        reject(error)
      })
    })
  },
  get_course({commit, state}, data) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      let pelatihan = data;
      let url = state.host+"/nJ403/7Y4dD2n3J9";

      Axios.post(url, pelatihan, {headers: headers})
      .then(response => {
        commit('set_loading', false);
        commit('set_course', response.data)
        resolve(response)
      })
      .catch(error => {
        console.log(error.response.data.message || error.message)
        commit('set_loading', false)
        commit('set_error', error)
        reject(error)
      })
    })
  },
  add_course({commit, state}, data) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      let pelatihan = data;
      let url = state.host+"/nJ403/q1e0HbT33";

      Axios.post(url, pelatihan, {headers: headers})
      .then(response => {
        commit('set_loading', false);
        commit('set_course', response.data)
        resolve(response)
      })
      .catch(error => {
        console.log(error.response.data.message || error.message)
        commit('set_loading', false)
        commit('set_error', error)
        reject(error)
      })
    })
  },
  add_course_by_smartbangkom({commit, state}, data) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      let pelatihan = data;
      let url = state.host+"/nJ403/2fzY1AsxqT5n";

      Axios.post(url, pelatihan, {headers: headers})
      .then(response => {
        commit('set_loading', false);
        commit('set_course', response.data)
        resolve(response)
      })
      .catch(error => {
        console.log(error.response.data.message || error.message)
        commit('set_loading', false)
        commit('set_error', error)
        reject(error)
      })
    })
  }
  // example({commit, state}) {
  //   return new Promise((resolve, reject) => {

  //   })
  // }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}