<template>
  <div style="background-color: #f1f5f9;">
    <div>
      <div class="flex h-screen flex-col items-center justify-center">
        <div class="card">
          <div class="mb-4">
            <h1 class="titillium-web-bold text-xl">Welcome to SWAJAR</h1>
            <p class="lato-regular text-gray-400 leading-tight text-sm">Please create your account</p>
          </div>
          <div>
            <el-input class="font-10" placeholder="Username" v-model="username"></el-input>
          </div>
          <div class="mt-1">
            <vue-hcaptcha sitekey="3c5049d3-e571-42d9-9565-73f8c5000c88" @verify="onVerify" @expired="onExpire" @challenge-expired="onChallengeExpire" @error="onError"></vue-hcaptcha>
          </div>
          <div class="mt-2" v-if="disabled_login">
            <el-button type="primary" size="small" class="font-10 uppercase w-full" :loading="get_loading" @click="register">Register or Sign Up</el-button>
          </div>
          <div class="mt-2" v-if="!disabled_login">
            <el-button type="primary" size="small" class="font-10 uppercase w-full" disabled>Register / Daftar</el-button>
          </div>
          <p class="titillium-web-light text-center text-xs mt-3 p-0">Sudah punya akun?  <a class="text-blue-500 cursor-pointer" @click="getViews('/')">Login</a></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VueHcaptcha from '@hcaptcha/vue-hcaptcha';
export default {
  name: 'RegisterView',
  components: {
    VueHcaptcha
  },
  data() {
    return {
      username: null,
      password: null,
      verified: false,
      expired: false,
      token: null,
      eKey: null,
      error: null,
    }
  },
  computed: {
    disabled_login() {
      if (this.username == null || this.username == "" || this.verified == false) {
        return false
      }
      return true
    },
    get_message() {
      return this.$store.state.message
    },
    get_loading() {
      return this.$store.state.loading
    }
  },
  methods: {
    getViews: function(value){
      if (this.$route.path !== value) {
        this.$router.push(value);
      }
    },
    register: function() {
      const username = this.username;
      this.$store.dispatch('register', { username })
      .then(() => {
        this.$router.push('/');
        this.username = null;
      })
      .catch(() => {
        
      })
    },
    onVerify(token, ekey) {
      this.verified = true;
      this.token = token;
      this.eKey = ekey;
    },
    onExpire() {
      this.verified = false;
      this.token = null;
      this.eKey = null;
      this.expired = true;
    },
    onChallengeExpire() {
      this.verified = false;
      this.token = null;
      this.eKey = null;
      this.expired = true;
    },
    onError(err) {
      this.token = null;
      this.eKey = null;
      this.error = err;
    },
  }
}
</script>

<style scoped>
.card {
  padding: 2.5rem;
  background-color: #FFF;
  /* box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px; */
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
