import Axios from 'axios'

const state = () => ({
  evaluation: {},
  question: [],
  test: [],
  message: null,
  info: [],
  status: null,
  error: null,
  code: null,
  loading: false,
  host: 'https://apifirstswajar.lan.go.id'
  // host: "http://10.67.0.152:8080"
  // host: "http://localhost:8080"
})

const mutations = {
  set_evaluation(state, evaluation){
    state.evaluation = evaluation
  },
  set_question(state, question){
    state.question = question
  },
  set_test(state, test){
    state.test = test
  },
  set_message(state, message){
    state.message = message
  },
  set_info(state, info){
    state.info = info
  },
  set_status(state, status){
    state.status = status
  },
  set_error(state, error){
    state.error = error
  },
  set_code(state, code){
    state.code = code
  },
  set_loading(state, loading){
    state.loading = loading
  }
}

const actions = {
  get_question({commit, state}, data) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      let question = data;
      let url = state.host+"/5oF84/52aDFovw0y";

      Axios.post(url, question, {headers: headers})
      .then(response => {
        let pertanyaan = response.data.data;
        let question = [];
        let info = [];

        for (let index = 0; index < pertanyaan.length; index++) {
          const element = pertanyaan[index];
          question.push({"soal": element.soal, "pilihan": JSON.parse(element.pilihan)})
          info.push({"paket": element.paket, "type": element.item, "keterangan": element.keterangan})
        }

        const unique_info = info.filter((obj, index, self) => 
          self.findIndex((o) => o.paket === obj.paket && o.type === obj.type && o.keterangan === obj.keterangan) === index
        );

        commit('set_loading', false);
        // commit('set_question', response.data)
        commit('set_question', question)
        // commit('set_test', question)
        commit('set_info', unique_info)
        resolve(response)
      })
      .catch(error => {
        // console.log(error.response.data.message || error.message)
        commit('set_loading', false)
        commit('set_error', error)
        reject(error)
      })
    })
  },
  add_evaluation({commit, state}, data) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      let question = data;
      let url = state.host+"/2b6I0/yWjX5jW25";

      Axios.post(url, question, {headers: headers})
      .then(response => {

        commit('set_loading', false);
        resolve(response)
      })
      .catch(error => {
        // console.log(error.response.data.message || error.message)
        commit('set_loading', false)
        commit('set_error', error)
        reject(error)
      })
    })
  },
  get_evaluation({commit, state}, data) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      let evaluation = data;
      let url = state.host+"/2b6I0/3uG32H4Tor";

      Axios.post(url, evaluation, {headers: headers})
      .then(response => {

        commit('set_loading', false);
        commit('set_evaluation', response.data)
        resolve(response)
      })
      .catch(error => {
        // console.log(error.response.data.message || error.message)
        commit('set_loading', false)
        commit('set_error', error)
        reject(error)
      })
    })
  },
  submit_evaluation({commit, state}, data) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      let evaluation = data;
      let url = state.host+"/2b6I0/2T9r2nDHz8";

      Axios.post(url, evaluation, {headers: headers})
      .then(response => {

        commit('set_loading', false);
        resolve(response)
      })
      .catch(error => {
        // console.log(error.response.data.message || error.message)
        commit('set_loading', false)
        commit('set_error', error)
        reject(error)
      })
    })
  },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}