import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'

import peserta from './modules/peserta'
import course from './modules/course'
import evaluation from './modules/evaluation'
import fileSwajar from './modules/file-swajar'
import certificate from './modules/certificate'

Vue.use(Vuex)

export default new Vuex.Store({
  strict: true,
  state: {
    token: sessionStorage.getItem("token") || null,
    message: null,
    status: null,
    user: null,
    role: {},
    info: {},
    code: null,
    loading: false,
    host: "https://apifirstswajar.lan.go.id"
    // host: "http://10.67.0.152:8080"
    // host: "http://localhost:8080"
  },
  getters: {
    isLoggedIn: state => !!state.token
  },
  mutations: {
    set_token(state, token){
      state.token = token
    },
    set_message(state, message){
      state.message = message
    },
    set_status(state, status){
      state.status = status
    },
    set_user(state, user){
      state.user = user
    },
    set_role(state, role){
      state.role = role
    },
    set_info(state, info){
      state.info = info
    },
    set_code(state, code){
      state.code = code
    },
    set_loading(state, loading){
      state.loading = loading
    },
    set_logout(state){
      state.token = null
      state.message = null
      state.status =  null
      state.user = null
      state.role = {}
      state.info = {}
      state.code = null
      state.loading = false
    }
  },
  actions: {
    login({commit, state}, user) {
      console.log(user);
      return new Promise((resolve, reject) => {
        commit('set_loading', true);
        // let url = 'http://localhost:8080/oauth/token?grant_type=password&username=199304232018011002&password=1371022304930005';
        let url = state.host+"/oauth/token?grant_type=password&username="+user.username+"&password="+user.password;
        // let url = "http://10.67.0.152:8080/oauth/token?grant_type=password&username="+user.username+"&password="+user.password;
        // let url = "https://apifirstswajar.lan.go.id/oauth/token?grant_type=password&username=198705122023212001&password=3578145205870002";
        // let data_json = {"username": user.username, "password": user.password};
        let header = {'Authorization': 'Basic aGlUMDg4eDY0clBBOm15LXNlY3JldA=='};
        Axios.post(url, " ", {headers: header})
        .then(response => {
          const token = response.data.access_token;
          if (token != null) {
            sessionStorage.setItem('token', token);
            Axios.defaults.headers.common['Authorization'] = token;
          }
          
          commit('set_loading', false)
          commit('set_token', token)
          resolve(response)
        })
        .catch(error => {
          commit('set_message', error.response.data.error_description)
          commit('set_loading', false)
          reject(error)
        })
      })
    },
    logout: function({commit}){
      return new Promise((resolve) => {
        commit('set_logout')
        sessionStorage.removeItem('token')
        localStorage.removeItem('page');
        delete Axios.defaults.headers.common['Authorization']
        resolve()
      })
    },
    register: function({commit, state}, user){
      return new Promise((resolve, reject) => {
        commit('set_loading', true);
        let url = state.host+"/2i8P9/Dr9C2x4111LT";
        let data_json = {"username": user.username};

        Axios.post(url, data_json)
        .then(response => {

          commit('set_message', response.data)
          commit('set_loading', false)
          resolve(response)
        })
        .catch(error => {
          commit('set_message', error.response.data.error_description)
          commit('set_loading', false)
          reject(error)
        })
      })
    },
    roles({ commit }) {
      let token = sessionStorage.getItem("token");
      let base64Url = token.split(".")[1];
      let base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
      let jsonPayload = decodeURIComponent(atob(base64).split("").map(function(c) {return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);}).join(""));
      let roles = JSON.parse(jsonPayload);
      commit("set_role", roles.authorities);
    },
  },
  modules: {
    peserta,
    course,
    evaluation,
    fileSwajar,
    certificate
  }
})
