          </div>
        </div>
        <div v-for="(item, index) in get_course" :key="index">
          <div class="flex justify-between items-center border-b px-0 pt-3 pb-3">
            <div class="flex-grow">
              <p class="titillium-web-regular p-0 m-0">{{ item.pelatihan }}</p>
              <p class="text-xs titillium-web-extralight p-0 m-0" v-if="item.pelatihan == 'SWAJAR MOT' || item.pelatihan == 'SWAJAR TOC'">{{ item.keterangan }}</p>
            </div>
            <div class="w-60">
              <p class="text-xs titillium-web-extralight p-0 m-0">Evaluasi Akademik</p>
              <p class="titillium-web-regular p-0 m-0"><span>{{ item.evaluasi_akademik[0] }}</span> <span v-if="item.pelatihan != 'SWAJAR PPPK'">/ Essay 0</span></p>
            </div>
            <div>
              <p class="text-xs titillium-web-extralight p-0 m-0" v-if="item.pelatihan == 'SWAJAR PPPK'">Sertifikat</p>

              <!-- {{ get_certificate }} -->
              <p class="titillium-web-regular cursor-pointer p-0 m-0" v-if="get_certificate.message == 'success' && item.evaluasi_akademik[0] != 0">{{ get_certificate.data.length > 0 ? get_certificate.data[0].nomor : 'Tidak ada' }}</p>
              <p class="titillium-web-regular cursor-pointer p-0 m-0" v-if="get_certificate.message == 'not found' && item.evaluasi_akademik[0] == 0">Harap menyelesaikan evaluasi akademik</p>
              <p class="titillium-web-regular cursor-pointer p-0 m-0" v-if="get_certificate.message == 'success' && item.evaluasi_akademik[0] == 0">Harap menyelesaikan evaluasi akademik</p>
                
              <span class="text-xs titillium-web-light border cursor-pointer hover:bg-gray-200 px-1 py-0.5 m-0" v-if="get_peserta.message == 'success'&& get_certificate.message == 'not found' && item.evaluasi_akademik[0] != 0" @click="add_sertifikat(get_peserta.data)">Ambil Nomor Sertifikat</span>
              
              <div v-if="get_certificate.message == 'success'">
                <span class="text-xs titillium-web-light border cursor-pointer hover:bg-gray-200 px-1 py-0.5 m-0" v-if="get_peserta.message == 'success' && get_photo.message == 'success' &&  get_certificate.message == 'success' && get_certificate.data[0].isActive == 0" @click="generate_sertifikat(get_peserta.data, get_photo.data, get_certificate.data[0], item)">Generate Sertifikat</span>
                <span class="text-xs titillium-web-light border cursor-pointer hover:bg-gray-200 px-1 py-0.5 m-0" v-if="get_peserta.message == 'success' && get_photo.message == 'success' && get_certificate.message == 'success' && get_certificate.data[0].isActive == 1"  @click="download_sertifikat(get_certificate.data[0].file)">Lihat Sertifikat</span>
                <span class="text-xs titillium-web-light border cursor-pointer hover:bg-gray-200 px-1 py-0.5 mx-1 my-0" v-if="get_peserta.message == 'success' && get_photo.message == 'success' && get_certificate.message == 'success' && get_certificate.data[0].isActive == 1"  @click="update_sertifikat_2(get_certificate.data[0])">Lanjut Proses TTE</span>
                <span class="text-xs titillium-web-light border cursor-pointer hover:bg-gray-200 px-1 py-0.5 mx-0 my-0" v-if="get_peserta.message == 'success' && get_photo.message == 'success' && get_certificate.message == 'success' && get_certificate.data[0].isActive == 2 || get_certificate.data[0].isActive == 3">Sertifikat sedang proses TTE</span>
                <span class="text-xs titillium-web-light border cursor-pointer hover:bg-gray-200 px-1 py-0.5 m-0" v-if="get_peserta.message == 'success' && get_photo.message == 'success' && get_certificate.message == 'success' && get_certificate.data[0].isActive == 4"  @click="download_sertifikat(get_certificate.data[0].file)">Download Sertifikat</span>
              </div>
              
              <p class="text-xs titillium-web-light text-red-500 p-0 m-0" v-if="get_photo.message == 'not found'">Harap upload photo</p>
              <p class="text-xs titillium-web-light text-red-500 p-0 m-0" v-if="get_peserta.message == 'not found'">Harap lakukan sinkron data</p>
            </div>
          </div>
        </div>
        <div class="flex justify-end items-center border-b px-0 pt-3 pb-3">
          <div class="flex-grow">
            <p class="titillium-web-regular p-0 m-0">PHOTO</p>
          </div>
          <div v-if="get_photo.data == null">
            <input type="file" id="file" ref="file" class="titillium-web-light" placeholder="Please input" @change="onFileChange()">
          </div>
          <div v-if="get_photo.data == null">
            <el-button size="small" icon="el-icon-upload2" class="titillium-web-light uppercase" :loading="info.loading" @click="upload_photo">Upload</el-button>
          </div>
          <div v-if="get_photo.data != null">
            <el-button size="small" icon="el-icon-download" class="titillium-web-light uppercase" :loading="info.loading" @click="download_photo(get_photo.data.file)">Download</el-button>
            <el-button size="small" icon="el-icon-delete" class="titillium-web-light uppercase" :loading="info.loading" @click="delete_photo(get_photo.data.code)"></el-button>
          </div>
        </div>
        <!-- <div class="absolute inset-x-0 bottom-0 border-t pt-3 pb-3" style="margin-right:2.5rem;margin-left:2.5rem;">
          <div class="flex justify-start items-center">
            <div>
              <LogoutIcon size="20" />
            </div>
            <div>
              <p class="nunito-200 text-sm p-0 m-0 cursor-pointer hover:text-red-500" @click="logout">Logout / Sign Out</p>
            </div>
          </div>
        </div> -->
      </div>
    </div>
    <el-dialog title="Tips" :visible.sync="dialogvisible" width="30%">
      <span>This is a message</span>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="dialogvisible = false">Cancel</el-button>
        <el-button type="primary" size="mini" @click="dialogvisible = false">Confirm</el-button>
      </span>
