<script>
import Axios from 'axios'
import { LogoutIcon } from "@vue-hero-icons/solid"
export default {
  name: 'HomeProfileView',
  components: {
    LogoutIcon
  },
  data() {
    return {
      form: {checked: false, file: {identity: null, name: null, size: 0, type: null}},
      info: {loading: false,tanggal: new Date(), waktu: 5400, active: {}, style: null, role: {}, type: null},
      dialogvisible: false
    }
  },
  filters: {
    format_birth : function(input) {
      const [name, date] = input.split(", ");
      
      const formattedName = name.charAt(0).toUpperCase() + name.slice(1).toLowerCase();
      
      const [day, month, year] = date.split("-");
      
      const monthNames = ["Januari", "Februari", "Maret", "April", "Mei", "Juni", "Juli", "Agustus", "September", "Oktober", "November", "Desember"];
      
      const monthName = monthNames[parseInt(month, 10) - 1];
      
      const formattedDate = `${formattedName}, ${day} ${monthName} ${year}`;
      
      return formattedDate;
    },
    format_date : function(input) {
      const [year, month, day] = input.split("-");
    
      const formattedDate = `${day}/${month}/${year}`;
    
      return formattedDate;
    },
    capitalizeTitle : function(input) {
      const parts = input.split(" - ");
        
      const capitalizeWords = (str) => {
        return str
          .toLowerCase() // Convert the entire string to lowercase
          .split(" ") // Split the string into words
          .map(word => word.charAt(0).toUpperCase() + word.slice(1)) // Capitalize the first letter of each word
          .join(" "); // Join the words back into a string
      };
        
      const formattedParts = parts.map(capitalizeWords);
        

      const formattedString = formattedParts.join(" - ");
        
      return formattedString;
    }
  },
  computed: {
    get_peserta() {
      return this.$store.state.peserta.peserta
    },
    get_course() {
      let all_course = this.$store.state.course.course.data;
      let course = [];

      for (let i = 0; i < all_course.length; i++) {
        const element = all_course[i];
        let evaluasiAkademik = element.evaluasiAkademik != 'Tidak ada' ? element.evaluasiAkademik.replace(/[\[\]]/g, '').split(',') : '0';
        let tanggal_1 = element.tanggal != null ? element.tanggal.replace(/[\[\]]/g, '').split(',') : 'Tidak ada';
        course.push({code: element.code, nip: element.nip, pelatihan: element.pelatihan, angkatan: element.angkatan, evaluasi_akademik: evaluasiAkademik, tanggal: tanggal_1, keterangan: element.keterangan})
      }

      return course;
    },
    get_certificate() {
      return this.$store.state.certificate.certificate.data.length > 0 ? this.$store.state.certificate.certificate : {message: 'not found', data: []}
    },
    get_photo() {
      return this.$store.state.fileSwajar.photo
    },
    get_peserta_loading() {
      return this.$store.state.peserta.loading
    },
    get_course_loading() {
      return this.$store.state.course.loading
    },
    get_certificate_loading() {
      return this.$store.state.certificate.loading
    },
    get_photo_loading() {
      return this.$store.state.fileSwajar.loading
    }
  },
  // mounted() {
  //   this.$store.dispatch('course/get_all_course')
  // },
  created(){
    this.$store.dispatch('peserta/get_peserta')
    this.$store.dispatch('course/get_all_course')
    this.$store.dispatch('certificate/get_certificate')
    this.$store.dispatch('fileSwajar/get_photo')
  },
  methods: {
    getViews: function(value){
			if (this.$route.path !== value) {
				this.$router.push(value);
			}
		},
    logout: function () {
      this.$store.dispatch("logout").then(() => {
        this.$router.push({ name: "home" });
      });
    },
    add_peserta: function() {
      this.$store.dispatch('peserta/add_peserta')
      .then(() => {
        this.$store.dispatch('peserta/get_peserta')
      })
    },
    onFileChange() {
      let file = this.$refs.file.files[0];
      this.form.file.identity = file;
      this.form.file.name = file.name;
      this.form.file.size = file.size;
      this.form.file.type = file.type;
      // console.log(this.form.file);
    },
    upload_photo: function () {
      this.info.loading = true;

      let url = "https://apifirstswajar.lan.go.id/297Nq/wZ9U93ey5";

      let headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};

      let formData = new FormData()
          formData.append('file', this.form.file.identity)

      Axios.post(url, formData, {headers: headers})
      .then(() => {
        this.info.loading = false;
        this.$store.dispatch('fileSwajar/get_photo')
      })
      .catch(error => {
        console.log(error);
      })
    },
    download_photo: function (value){
      this.info.loading = false;
      let url = 'https://apifirstswajar.lan.go.id/297Nq/2gFQ1PAnb0Q2/'+value;
      let headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      Axios.get(url, {headers: headers, responseType: 'blob'})
      .then(response => {
        this.info.loading = false;
        window.open(URL.createObjectURL(response.data));
      })
      .catch(error => {
        console.log(error);
      })
    },
    delete_photo: function (value){
      this.$store.dispatch('fileSwajar/delete_photo', {code: value})
      .then(() => {
        this.$store.dispatch('fileSwajar/get_photo')
      })
    },
    add_sertifikat: function (value) {
      let nama_file = this.generateRandomPassword(15);
      let datasertifikat = {file: nama_file+".pdf", filename: nama_file, instansi: value.instansi, nama: this.format_name(value.nama), nip: value.nip, tahun: new Date().getFullYear(), url_filename: "Tidak ada"};
      // console.log(datasertifikat);

      this.$store.dispatch('certificate/add_certificate', datasertifikat)
      .then(() => {
        this.$store.dispatch('certificate/get_certificate')
      })
    },
    generate_sertifikat: function (user, photo, certificate, course) {
      let generatesertifikat = {"fileName": certificate.file, "filePhoto": photo.file, "nomor": certificate.nomor, "tahun": certificate.tahun, "tanggalAwal": this.format_date(course.tanggal[0]), "tanggalAkhir": this.format_date(course.tanggal[1]), "tempat": "Jakarta", "nama": this.format_name(user.nama), "nip": user.nip, "jabatan": this.capitalizeTitle(user.jabatan), "golonganPangkat": user.golonganPangkat, "instansi": user.instansi, "tempatTanggalLahir": this.format_birth(user.tempatTanggalLahir), "nilai": this.konversi_int(course.evaluasi_akademik[0]), "tanggal": this.format_date_to_indonesian(certificate.createdAt)};
      // console.log(generatesertifikat);

      this.$store.dispatch('certificate/generate_certificate', generatesertifikat)
      .then(response => {
        if (response.data == "successfully") {
          this.update_sertifikat(certificate)
        }
        this.$store.dispatch('certificate/get_certificate')
      })
    },
    update_sertifikat: function (certificate) {
      let generatesertifikat = {code: certificate.code, createdAt: certificate.createdAt, file: certificate.file, filename: certificate.filename, instansi: certificate.instansi, isActive: 1, isDeleted: false, keterangan: certificate.keterangan, nama: certificate.nama, nip: certificate.nip, nomor: certificate.nomor, tahun: certificate.tahun, updatedAt: certificate.updatedAt, url_filename: certificate.url_filename};
      // console.log(generatesertifikat);

      this.$store.dispatch('certificate/update_certificate', generatesertifikat)
      .then(() => {
        this.$store.dispatch('certificate/get_certificate')
      })
    },
    update_sertifikat_2: function (certificate) {
      let generatesertifikat = {code: certificate.code, createdAt: certificate.createdAt, file: certificate.file, filename: certificate.filename, instansi: certificate.instansi, isActive: 2, isDeleted: false, keterangan: certificate.keterangan, nama: certificate.nama, nip: certificate.nip, nomor: certificate.nomor, tahun: certificate.tahun, updatedAt: certificate.updatedAt, url_filename: certificate.url_filename};
      // console.log(generatesertifikat);

      this.$store.dispatch('certificate/update_certificate', generatesertifikat)
      .then(() => {
        this.$store.dispatch('certificate/get_certificate')
      })
    },
    download_sertifikat: function (value){
      this.info.loading = false;
      let url = 'https://apifirstswajar.lan.go.id/sRkvy/oVGL4EDlRtrR/'+value;
      let headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      Axios.get(url, {headers: headers, responseType: 'blob'})
      .then(response => {
        this.info.loading = false;
        window.open(URL.createObjectURL(response.data));
      })
      .catch(error => {
        console.log(error);
      })
    },
    format_name: function (name) {
      const parts = name.split(' ');

      const formattedParts = parts.map(part => {
        return part.charAt(0).toUpperCase() + part.slice(1).toLowerCase();
      });

      return formattedParts.join(' ');
    },
    format_birth : function(input) {
      const [name, date] = input.split(", ");
      
      const formattedName = name.charAt(0).toUpperCase() + name.slice(1).toLowerCase();
      
      const [day, month, year] = date.split("-");
      
      const monthNames = ["Januari", "Februari", "Maret", "April", "Mei", "Juni", "Juli", "Agustus", "September", "Oktober", "November", "Desember"];
      
      const monthName = monthNames[parseInt(month, 10) - 1];
      
      const formattedDate = `${formattedName}, ${day} ${monthName} ${year}`;
      
      return formattedDate;
    },
    format_date : function(input) {
      const [year, month, day] = input.split("-");
    
      const formattedDate = `${day}/${month}/${year}`;
    
      return formattedDate;
    },
    format_date_to_indonesian : function(input) {
      const date = new Date(input);
    
      const day = date.getDate(); // Get the day (1-31)
      const monthIndex = date.getMonth(); // Get the month (0-11)
      const year = date.getFullYear(); // Get the year (e.g., 2024)

      const monthNames = ["Januari", "Februari", "Maret", "April", "Mei", "Juni", "Juli", "Agustus", "September", "Oktober", "November", "Desember" ];

      const monthName = monthNames[monthIndex];

      const formattedDate = `${day} ${monthName} ${year}`;

      return formattedDate;
    },
    capitalizeTitle : function(input) {
      const parts = input.split(" - ");
        
      const capitalizeWords = (str) => {
        return str
          .toLowerCase() // Convert the entire string to lowercase
          .split(" ") // Split the string into words
          .map(word => word.charAt(0).toUpperCase() + word.slice(1)) // Capitalize the first letter of each word
          .join(" "); // Join the words back into a string
      };
        
      const formattedParts = parts.map(capitalizeWords);
        

      const formattedString = formattedParts.join(" - ");
        
      return formattedString;
    },
    generateRandomPassword: function(length) {
      const uppercase = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
      const lowercase = 'abcdefghijklmnopqrstuvwxyz';
      const numbers = '0123456789';
      const allCharacters = uppercase + lowercase + numbers;
      
      let password = '';
      for (let i = 0; i < length; i++) {
          const randomIndex = Math.floor(Math.random() * allCharacters.length);
          password += allCharacters[randomIndex];
      }
      
      return password;
    },
    konversi_int: function(value) {
      let floatValue = value == 70.01 ? 72 : value;
      let intValue = parseInt(floatValue);
      // console.log(intValue);  // Output: 88 
      return intValue
    }
  },
}
</script>

