import Axios from 'axios'

const state = () => ({
  certificate: {},
  message: null,
  info: {},
  status: null,
  error: null,
  code: null,
  loading: false,
  host: 'https://apifirstswajar.lan.go.id'
  // host: "http://10.67.0.152:8080"
  // host: "http://localhost:8080"
})

const mutations = {
  set_certificate(state, certificate){
    state.certificate = certificate
  },
  set_message(state, message){
    state.message = message
  },
  set_info(state, info){
    state.info = info
  },
  set_status(state, status){
    state.status = status
  },
  set_error(state, error){
    state.error = error
  },
  set_code(state, code){
    state.code = code
  },
  set_loading(state, loading){
    state.loading = loading
  }
}

const actions = {
  get_certificate({commit, state}, data) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      // let pelatihan = data;EloilowNST
      // let url = state.host+"/1By99/zOizhXDufV";
      // let url = state.host+"/1By99/EloilowNST";
      // let url = state.host+"/sRkvy/EloilowNST";
      let url = state.host+"/sRkvy/4V39sY0mYa";

      Axios.get(url, {headers: headers})
      .then(response => {
        commit('set_loading', false);
        commit('set_certificate', response.data)
        resolve(response)
      })
      .catch(error => {
        console.log(error.response.data.message || error.message)
        commit('set_loading', false)
        commit('set_error', error)
        reject(error)
      })
    })
  },
  generate_certificate({commit, state}, data) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      let certificate = data;
      // let url = "https://localhost:8080/sRkvy/NdcKFI5029";
      let url = state.host+"/sRkvy/NdcKFI5029";

      Axios.post(url, certificate, {headers: headers})
      .then(response => {
        commit('set_loading', false);
        commit('set_certificate', response.data)
        resolve(response)
      })
      .catch(error => {
        console.log(error.response.data.message || error.message)
        commit('set_loading', false)
        commit('set_error', error)
        reject(error)
      })
    })
  },
  update_certificate({commit, state}, data) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      let certificate = data;
      // let url = "https://localhost:8080/sRkvy/NdcKFI5029";
      let url = state.host+"/sRkvy/ou8Hv54W9N/"+data.code;

      Axios.post(url, certificate, {headers: headers})
      .then(response => {
        commit('set_loading', false);
        commit('set_certificate', response.data)
        resolve(response)
      })
      .catch(error => {
        console.log(error.response.data.message || error.message)
        commit('set_loading', false)
        commit('set_error', error)
        reject(error)
      })
    })
  },
  add_certificate({commit, state}, data) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      let certificate = data;
      // let url = state.host+"/1By99/U1F6hSa850";
      let url = state.host+"/sRkvy/U1F6hSa850";

      Axios.post(url, certificate, {headers: headers})
      .then(response => {
        commit('set_loading', false);
        commit('set_certificate', response.data)
        resolve(response)
      })
      .catch(error => {
        console.log(error.response.data.message || error.message)
        commit('set_loading', false)
        commit('set_error', error)
        reject(error)
      })
    })
  }
  // example({commit, state}) {
  //   return new Promise((resolve, reject) => {

  //   })
  // }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}