import Axios from 'axios'

const state = () => ({
  jurnal: {},
  photo: {},
  test: {},
  message: null,
  info: {},
  status: null,
  error: null,
  code: null,
  loading: false,
  host: 'https://apifirstswajar.lan.go.id'
  // host: "http://10.67.0.152:8080"
  // host: "http://localhost:8080"
})

const mutations = {
  set_jurnal(state, jurnal){
    state.jurnal = jurnal
  },
  set_photo(state, photo){
    state.photo = photo
  },
  set_test(state, test){
    state.test = test
  },
  set_message(state, message){
    state.message = message
  },
  set_info(state, info){
    state.info = info
  },
  set_status(state, status){
    state.status = status
  },
  set_error(state, error){
    state.error = error
  },
  set_code(state, code){
    state.code = code
  },
  set_loading(state, loading){
    state.loading = loading
  }
}

const actions = {
  get_jurnal({commit, state}, data) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      let evaluation = data;
      let url = state.host+"/0L4r8/V9n5751Cln";

      Axios.post(url, evaluation, {headers: headers})
      .then(response => {

        commit('set_loading', false);
        commit('set_jurnal', response.data)
        resolve(response)
      })
      .catch(error => {
        // console.log(error.response.data.message || error.message)
        commit('set_loading', false)
        commit('set_error', error)
        reject(error)
      })
    })
  },
  delete_jurnal({commit, state}, data) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      let url = state.host+"/0L4r8/8Bvlf7eSzR2s/"+data.code;

      Axios.get(url, {headers: headers})
      .then(response => {

        commit('set_loading', false);
        commit('set_jurnal', response.data)
        resolve(response)
      })
      .catch(error => {
        // console.log(error.response.data.message || error.message)
        commit('set_loading', false)
        commit('set_error', error)
        reject(error)
      })
    })
  },
  get_photo({commit, state}, data) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      // let evaluation = data;
      let url = state.host+"/297Nq/jRVqP4F27t";

      Axios.get(url, {headers: headers})
      .then(response => {

        commit('set_loading', false);
        commit('set_photo', response.data)
        resolve(response)
      })
      .catch(error => {
        // console.log(error.response.data.message || error.message)
        commit('set_loading', false)
        commit('set_error', error)
        reject(error)
      })
    })
  },
  delete_photo({commit, state}, data) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      let url = state.host+"/297Nq/qzCNNR99BS6J/"+data.code;

      Axios.get(url, {headers: headers})
      .then(response => {

        commit('set_loading', false);
        commit('set_photo', response.data)
        resolve(response)
      })
      .catch(error => {
        // console.log(error.response.data.message || error.message)
        commit('set_loading', false)
        commit('set_error', error)
        reject(error)
      })
    })
  },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}