import Vue from 'vue'
import VueRouter from 'vue-router'
import Store from '../store'

import HomeView from '../views/HomeView.vue'
import AboutView from '../views/AboutView.vue'

import HomeMediaView from '../views/media/HomeView.vue'
import HomeSirelaView from '../views/sirela/HomeView.vue'
import HomeProfileView from '../views/profile/HomeView.vue'

import RegisterView from '../views/RegisterView.vue'
import ComingSoonView from '../views/ComingSoonView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: {
      requiresVisitor: true,
    }
  },
  {
    path: '/9edN7my6c66f',
    name: 'about',
    component: AboutView,
    meta: {
      requiresAuth: true,
    }
  },
  {
    path: '/F6D2wQo5g6Ka/z1s6QUD8D2Cx/:id',
    name: 'HomeMediaView',
    component: HomeMediaView,
    meta: { 
      requiresAuth: true
    }
  },
  {
    path: '/603m5d3M5kE6',
    name: 'HomeSirelaView',
    component: HomeSirelaView,
    meta: { 
      requiresAuth: true
    }
  },
  {
    path: '/tP4KJ16Q8wm2',
    name: 'HomeProfileView',
    component: HomeProfileView,
    meta: { 
      requiresAuth: true
    }
  },
  {
    path: '/PBcw3g0kC0xt',
    name: 'RegisterView',
    component: RegisterView,
    meta: {
      requiresVisitor: true,
    }
  },
  {
    path: '/hPH1L55V2bdn',
    name: 'ComingSoonView',
    component: ComingSoonView,
    meta: { 
      requiresAuth: true
    }
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!Store.getters.isLoggedIn) {
      next({ path: '/' })
    }
    next()
  } else if (to.matched.some(record => record.meta.requiresVisitor)) {
    if (Store.getters.isLoggedIn) {
      next({ path: '/9edN7my6c66f' })
    }
    next()
  }
})

export default router
