import Axios from 'axios'

const state = () => ({
  peserta: {},
  message: null,
  info: {},
  status: null,
  code: null,
  loading: false,
  host: 'https://apifirstswajar.lan.go.id'
  // host: "http://10.67.0.152:8080"
  // host: "http://localhost:8080"
})

const mutations = {
  set_peserta(state, peserta){
    state.peserta = peserta
  },
  set_message(state, message){
    state.message = message
  },
  set_info(state, info){
    state.info = info
  },
  set_status(state, status){
    state.status = status
  },
  set_code(state, code){
    state.code = code
  },
  set_loading(state, loading){
    state.loading = loading
  }
}

const actions = {
  get_peserta({commit, state}) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      let url = state.host+"/961Xv/8b5zG9EL7P";

      Axios.get(url, {headers: headers})
      .then(response => {
        commit('set_loading', false);
        commit('set_peserta', response.data)
        resolve(response)
      })
      .catch(error => {
        commit('set_loading', false)
        reject(error)
      })
    })
  },
  add_peserta({commit, state}) {
    return new Promise((resolve, reject) => {
      commit('set_loading', true);
      const headers = {'Authorization': 'Bearer '+sessionStorage.getItem('token')};
      let url = state.host+"/961Xv/psvQZQZ39m3h";

      Axios.get(url, {headers: headers})
      .then(response => {
        commit('set_loading', false);
        commit('set_peserta', response.data)
        resolve(response)
      })
      .catch(error => {
        commit('set_loading', false)
        reject(error)
      })
    })
  }
  // example({commit, state}) {
  //   return new Promise((resolve, reject) => {

  //   })
  // }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}